import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/Index'
import Project from "../views/Project";
import Faq from "../views/FAQ";
import Roll from "../views/Roll";
import RollResult from "../views/RollResult";
import Story from "../views/Story";
import Info from "../views/Info";
import UserComment from "../views/UserComment";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Index
  },
  {
    path: '/project',
    component: Project
  },
  {
    path: '/faq',
    component: Faq
  },
  {
    path: '/info',
    component: Info,
  },
  {
    path: '/roll',
    component: Roll
  },
  {
    path: '/rollresult',
    component: RollResult
  },
  {
    path: '/story',
    component: Story,
  },
  {
    path: '/user/comment',
    component: UserComment,
  },
]

const router = new VueRouter({
  routes
})

export default router
